@mixin font-face($family, $filename, $style, $weight) {
  @font-face {
    font-family: $family;
    src: url("../fonts/" + $filename + ".eot");
    src: url("../fonts/" + $filename + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $filename + ".woff2") format("woff2"), url("../fonts/" + $filename + ".woff") format("woff"), url("../fonts/" + $filename + ".ttf") format("truetype"), url("../fonts/" + $filename + ".svg#svgFontName") format("svg");
    font-style: $style;
    font-weight: $weight;
  }
}
